@import "./common/c1-styles/s1-sass/variables";

* {
  margin: 0;
  padding: 0;
  outline: 0;
}

body{
  font-family: $primaryFontFamily;
  background-color: $primaryBackground;
  color: $secondaryColor;
  a{
    text-decoration: none;
    color: $secondaryColor;
  }
  a:hover{
    color: $primaryColor;
  }
}